import React, { useState, useEffect } from 'react';
import './style.css';

function Clock() {

  function getHours() {
    return(new Date().getHours() % 12)
  };

  function getMinutes() {
    return(new Date().getMinutes())
  };

  function getSeconds() {
    return(new Date().getSeconds())
  };
  
  const [time, setTime] = useState({
    hours: getHours(),
    minutes: getMinutes(),
    seconds: getSeconds()
  });
 
  useEffect(() => {

    // this will run when our Clock component did mount
    const tick = () => {
      setTime({
        hours: getHours(),
        minutes: getMinutes(),
        seconds: getSeconds()
      })
    };

    const timer = setInterval(tick, 1000);

    // returned function will be called on component unmount 
    return () => {
      clearInterval(timer);
    }
  }, []);

  let rotation = {
    hours: (360/12)*time.hours+(360/12/60)*time.minutes,
    minutes: (360/60)*time.minutes+(360/60/60)*time.seconds,
    seconds: (360/60)*time.seconds
  };

  let digits = [12, 3, 6, 9];

  return (
    <div className="Clock">

      {
        digits.map(digit => (
          <span key={digit} className={"digit digit"+digit}>{digit}</span>
        ))
      }

      <div className="dot" />

      <div className="second hand"
        style={{transform: `rotate(${rotation.seconds}deg)`}}
      />

      <div className="minute hand"
        style={{transform: `rotate(${rotation.minutes}deg)`}}
      />

      <div className="hour hand"
        style={{transform: `rotate(${rotation.hours}deg)`}}
      />

    </div>
  );
}

export default Clock;
